import wrap from '@/components/wrap';
import Dialog from '../../wxcomponents/vant/dialog/dialog';
export default {
  methods: {
    onClose({detail}) {
      const {position, instance} = detail;
      switch (position) {
        case 'left':
        case 'cell':
          instance.close();
          break;
        case 'right':
          Dialog.confirm({
            message: '确定删除吗？',
          }).then(() => {
            console.log('close');
            instance.close();
          });
          break;
      }
    },
  },
  components: {
    wrap,
  },
};
